import React from 'react'

const Contact = () => {
  return (
    <section id='contact'>
        <div className='contact-title' style={{margin:"auto",width:"auto",textAlign:"center"}}>
        <h3>Contact</h3>
      
        </div>
      <div style={{border:"1px solid red", height:"300px", width:"80%",margin:"auto"}}>

      </div>
    </section>
  )
}

export default Contact

